/* カスタムカバー全体：エディター上で全幅表示（グローバルな max-width を上書き） */
.wp-block-integlight-custom-cover {
    border: 1px dashed #ccc;
    padding: 20px 0;
    max-width: none !important;
    width: 100% !important;
}

/* カスタムカバーの直下の要素は全幅にする */
.wp-block-integlight-custom-cover>* {
    max-width: none !important;
    width: 100% !important;
}

/* 内側コンテンツコンテナの基本スタイル */
.wp-block-integlight-custom-cover .inner-container {
    padding: 0px;
    margin: 0 auto;
}

/* 内側コンテンツが全幅の場合 */
.wp-block-integlight-custom-cover .inner-container.inner-full {
    max-width: none !important;
    width: 100% !important;
}

/* 内側コンテンツが記事幅の場合（例: 最大幅960pxに制限） */
.wp-block-integlight-custom-cover .inner-container.inner-article {
    max-width: var(--ly-content-max-width) !important;
    width: 100% !important;
}